@import "../assets/fonts/stylesheet.scss";

:root {
  --primary-color: #072ac8;
}

* {
  font-family: "HK Grotesk Legacy";
  padding: 0;
  margin: 0;
  background: transparent;
  box-sizing: border-box;
  text-decoration: none;
  list-style: none;
  color: #253858;
  outline: none;
}

body {
  font-size: 16px;
  height: 100vh;
  overflow: hidden;
}

.app-container {
  padding-left: 60px;
  padding-right: 60px;

  &__section {
    max-width: 1138px;
    margin-right: auto;
    margin-left: auto;
  }
}

button {
  cursor: pointer;
}

.btn {
  background: var(--primary-color);
  border: none;
  color: #fff;
  padding: 18px 30px;
  border-radius: 100vh;
  font-weight: 500;
  font-size: 14px;
  display: flex;
  align-items: center;
  justify-content: space-between;

  &--primary {
    @extend .btn;
  }

  &--invert {
    @extend .btn;
    border: 1px solid var(--primary-color);
    color: var(--primary-color);
    background-color: rgba(7, 42, 200, 0.05);
  }
}

.home__ready {
  display: flex;
  align-items: center;

  &-container {
    background: #0a0a44;
  }

  .image {
    min-height: 600px;
  }

  .text {
    &,
    * {
      color: #fff;
    }

    h1 {
      font-size: 58px;

      strong {
        color: var(--primary-color);
      }
    }
    p {
      font-size: 22px;
      max-width: 388px;
      line-height: 1.6;
      margin: 24px 0;
      opacity: 0.8;
    }
  }

  &__btn-holder {
    display: flex;
    align-items: center;
    margin-top: 50px;

    button {
      font-size: 16px;
      margin-right: 20px;

      img {
        margin-left: 30px;
      }
    }
  }
}

.color-gradient {
  background: linear-gradient(90deg, #075ef7, #072ac8, #728af3, #075ef7);
  width: fit-content;
  background-clip: text;
  animation-name: shimmer;
  animation-duration: 6s;
  animation-fill-mode: forwards;
  animation-iteration-count: infinite;

  @keyframes shimmer {
    50% {
      background-position-x: 300px;
    }
  }

  *,
  & {
    color: transparent !important;
  }
}

main {
  position: absolute;
  padding-top: 82px;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  overflow: auto;
}

