// custom
.fade-enter {
  opacity: 0;
  z-index: 1;
  transform: translate(0, 30px);
}

.fade-enter-done {
  opacity: 1;
  transform: translate(0, 0);
  transition: 300ms ease-out;
}

.fade-exit {
  &:after {
    content: "";
    position: fixed;
    bottom: 0;
    top: 0;
    width: 100%;
    height: 100%;
    z-index: 2;
    background: rgb(255, 255, 255);
    transform: translate(0, 100%);
  }
}

.fade-exit.fade-exit-active {
  &:after {
    transform: translate(0, 0);
    transition: 400ms ease-out;
  }
}
